<template>
  <div>
    <div class="page-items">
      <Filters></Filters>
      <div>
          <Search 
            @search="querySearch" 
            @state="getState"
            :defaultItems="items.items"></Search>
        <b-container class="items-container">
          <el-card :body-style="{ padding: '0px' }" v-for="item in items.items" :key="item.id">
            <img
              src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
              class="image"
            />
            <div style="padding: 20px;">
              <h3 class="card__title">{{item.title}}</h3>
              <div class="bottom">
                <div class="author">
                  <span class="author__label">Trouvé par</span>
                  <span class="author__nom">{{item.author.firstname}} {{item.author.lastname}}</span>
                </div>
                <div class="category">
                  <span class="category__label">Catégorie</span>
                  <span class="category__nom">{{item.category}}</span>
                </div>
                <div class="date">
                  <span class="date__label">Trouvé le</span>
                  <span class="date__created">{{ item.created.date | dateFilter }}</span>
                </div>
                <b-button
                  variant="dark"
                  :to="{name: 'Item', params: {id : item.id}}"
                >Je suis le propiétaire</b-button>
              </div>
            </div>
          </el-card>
        </b-container>
      </div>
    </div>
  </div>
</template>

<script>
  import {Global} from '../../Global'
    // Libraries
    import axios from "axios";
    import moment from "moment";
    // custom components
    import Filters from './Filters';
    import Search from './search';

    export default {

    name: "ItemsFound",

    mounted() {
        this.getItems();
        moment.locale('fr');
    },
    components: {
        'Filters' : Filters,
        'Search'  : Search,
    },

    data() {
      return {
        VUE_APP_URL: Global.VUE_APP_URL,
        items: [],
        defaultItems: [],
      };
    },

    filters: {
        dateFilter: function(value) {
          return value ? moment(value).format("DD MMMM YYYY") : "";
        }
    },

    methods: {
        getItems() {
          axios.get(this.VUE_APP_URL + "items").then(res => {
              if (res.data.status === "success") {
              this.items = res.data;
              this.defaultItems = res.data;
              }
          }).catch((error) => {
            console.log(error);
          });
        },

        querySearch(items) {
          this.items.items = items;
        },
        getState(state) {
          if(state === " " || state === "") {
            this.getItems();
          }
        }
    }
    };
</script>

